<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process215"></a>

<p><br></p>
<p><span class="rvts17">埋葬仪式（</span><span class="rvts50">Burial Rites</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts41">坐标：</span>
<br><span class="rvts20">X:504,Y:318</span><span class="rvts21">，精灵营地</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">完成精灵的埋葬仪式</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1277.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">在锯木厂附近，有一处秘密的精灵营地，这里正在举行某种埋葬仪式。尝试说服精灵看守，进入埋葬仪式，通过一系列的</span><span class="rvts20"></span><span class="rvts21">尊重</span><span class="rvts20"></span><span class="rvts21">话语，完成仪式，该任务将关闭。</span>
<br><span class="rvts21">如果队伍中有精灵角色，会有额外的精灵专属标签语句，使该任务变得更加简单，然而说服门口的两个守卫需要较高的</span><span class="rvts20"></span><span class="rvts21">劝说</span><span class="rvts20"></span><span class="rvts21">值。</span>
<br><span class="rvts21">埋葬仪式顺利结束后，如果萨黑拉在欢乐堡未被杀掉，那么他们会给我们</span><span class="rvts20"></span><span class="rvts21">精灵先知（</span><span class="rvts20">The Elven Seer</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">支线，营救被困在锯木厂的萨黑拉。</span>
<br><span class="rvts21">如果萨黑拉在欢乐堡被我们杀掉了，有很大几率会被这个精灵营地敌对。如果可以通过更高的</span><span class="rvts20"></span><span class="rvts21">劝说</span><span class="rvts20"></span><span class="rvts21">值避免了战斗，那么他们会给予我们</span><span class="rvts20"></span><span class="rvts21">为死者报仇（</span><span class="rvts20">Vengence for the Fallen</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">支线，一样是需要杀掉锯木厂孤狼首领罗斯特（</span><span class="rvts20">Roost</span><span class="rvts21">）。</span>
<br><span class="rvts21">如果队伍中没有精灵角色，那么劝说精灵营地外的两个看守会略有难度，可以尝试去伐木场拯救萨黑拉后，再返回营地，则使得营地所有精灵变得友好，那么该支线将瞬间获得并瞬间以好的结局关闭。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



